      <mt-index-list>
        <mt-index-section  v-for="item,index in arr" :index="item.initial" :key="index">
          <mt-cell v-for="cell,num in item.list" :title="cell.country" @click.native="gjClick(cell.country,cell.country_errcode)" :key="num">
