<script>
  export default {
    data () {
      return {
        postData: {
          tel: '',
          errcode: '',
          terminal: 3,
          country_errcode: 86
        },
        num: 60,
        flag: false,
        isDisb: false,
        popupRight: false,
        isCountry: false,
        search: '',
        gj: '????????????',
        arr: []
      }
    },
    methods: {
      closeSearch (index) {
        let self = this
        self.search = ''
        if (index==1) {
          self.isCountry = false
        }
      },
      gjClick (cell, country) {
        let self = this
        self.gj = cell
        self.search = ''
        self.postData.country_errcode = country
        self.isCountry = false
      },
      countryClick () {
        let self = this
        self.isCountry = true
      },
      popupShow () {
        let self = this
        self.popupRight = true
        document.title = '????????????????????????'
      },
      popupHide () {
        let self = this
        self.popupRight = false
        document.title = '????????????????????????'
      },
      showTime () {
        let self = this
        if (!self.flag) {
          let reg = /^1(3|4|5|7|8)\d{9}$/
          if (!self.postData.tel) {
            this.$toast('????????????????????????')
            return false
          } else if (!reg.test(self.postData.tel)) {
            this.$toast('????????????????????????')
            return false
          }
          self.flag = true
          let timeShow = setInterval(function () {
            self.isDisb = true
            if (self.num <= 0) {
              self.flag = false
              self.isDisb = false
              clearInterval(timeShow)
              self.num = 60
            } else {
              self.num--
            }
          }, 1000)
        } else {
          return false
        }
      },
      sub () {
        let self = this
        let reg = /^1(3|4|5|7|8)\d{9}$/
        var regNum = /^\d{6}$/
        if (!self.postData.tel) {
          this.$toast('????????????????????????')
          return false
        } else if (!reg.test(self.postData.tel)) {
          this.$toast('????????????????????????')
          return false
        } else if (!self.postData.errcode) {
          this.$toast('??????????????????????????????????????????')
          return false
        } else if (!regNum.test(self.postData.errcode)) {
          this.$toast('??????????????????????????????????????????')
          return false
        } else {
          this.$http.post(`${process.env.API.USER}/login`, self.postData).then((res) => {
            if (!res.errcode) {
              self.$toast('????????????????????????')
              localStorage.setItem('userToken', JSON.stringify(res.data.token))
              setTimeout(() => {
                location.href = `${process.env.URL.MARKET}/#/?uid=${res.data.uid}`
              }, 1000)
            }
          })
        }
      }
    },
    mounted () {
      let self = this
      self.$i18n.locale = 'zh'
      self.$http.get(`${process.env.API.DICT}/dict/country`).then(function (res) {
        if (!res.errcode) {
          self.arr = res.data.data
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    watch: {
      search () {
        let self = this
        if (!self.search) {
          self.$http.get(`${process.env.API.DICT}/dict/country`).then(function (res) {
            if (!res.errcode) {
              self.arr = res.data.data
            }
          }).catch(function (err) {
            console.log(err)
          })
        } else {
          self.$http.get(`${process.env.API.DICT}/dict/country?country=${self.search}`).then(function (res) {
            if (!res.errcode) {
              self.arr = []
              self.arr = res.data.data
            }
          }).catch(function (err) {
            console.log(err)
          })
        }
      }
    }
  }
</script>
