import Vue from 'vue'
import VueI18n from 'vue-i18n'
import zh from '@/language/zh'
import en from '@/language/en'

Vue.use(VueI18n)

export default new VueI18n({
  locale: 'en',
  messages: {
    zh,
    en
  }
})



